import React, { useState, useEffect, useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'

import styles from './UserLoginPage.module.scss'
import { Myaxios } from '../../constants'
import MyContext from '../../Components/MyContext'

// Components
import Button from '../../Components/Button'
import AuthInput from '../../Components/AuthInput'
import { ReactComponent as AcLogo } from '../../assets/icons/AcLogo.svg'

function UserLoginPage() {
	const { updateUserData } = useContext(MyContext)
	const navigate = useNavigate()
	const [account, setAccount] = useState('')
	const [password, setPassword] = useState('')
	const [note, setNote] = useState({})
	// 
	const handleClick = () => {
		if (!password || !account) return setNote({ password: '' })
		//  POST /api/users/login
		Myaxios().post('/users/login', { account, password })
			.then(response => {
				const { token, user } = response.data.data
				user.currentUser = true
				console.log('login token', token)
				if (token) {
					localStorage.setItem('token', token)
					updateUserData({ token: token, user: user })
					navigate('/home')
				}
				setNote({})
				//catch !token
			})
			.catch(err => {
				//error
				if (err.response.status === 400) {
					setNote({ account: '' })
				}
				if (err.response.status === 401) {
					setNote({ password: '' })
				}
			})
	}
	return (
		<div className={styles.container}>
			<div>
				<AcLogo />
			</div>
			<h3 className={styles.title}>Alphitter</h3>
			{/* AuthInput group */}
			<div className={styles['authinput-group']}>
				<AuthInput label='' type='text' value={account} placeholder='' onChange={setAccount} note={note.account} />

				<AuthInput label='' type='password' value={password} placeholder='' onChange={setPassword} note={note.password} />
			</div>
			<div className={styles['auth-button']} onClick={handleClick}>
				<Button styleName='lg-bg-logo'></Button>
			</div>
			{/*  */}
			<div className={styles['auth-link-box']}>
				<Link to='/register' className={styles['auth-link']}>
					<div className={styles['auth-link-text']}></div>
				</Link>
				<div className={styles['auth-link-dot']}></div>
				<Link to='/admin' className={styles['auth-link']}>
					<div className={styles['auth-link-text']}></div>
				</Link>
			</div>
		</div>
	)
}

export default UserLoginPage
