import React, { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'

import styles from './RegisterPage.module.scss'
import { Myaxios } from '../../constants'
import { takeErrMsg } from '../../utils'

//Components
import { ReactComponent as AcLogo } from '../../assets/icons/AcLogo.svg'
import AuthInput from '../../Components/AuthInput'
import Button from '../../Components/Button'
import Alert from '../../Components/Alert'

function RegisterPage() {
	const navigate = useNavigate()
	//
	const [account, setAccount] = useState('')
	const [name, setName] = useState('')
	const [email, setEmail] = useState('')
	const [password, setPassword] = useState('')
	const [checkPassword, setCheckPassword] = useState('')
	const [note, setNote] = useState({})
	const [alertNote, setAlertNote] = useState('')
	function updateNoteField(field, value) {
		if (field === 'password' && value) {
			const regExp = new RegExp('^(?=.*[a-z])(?=.*[0-9])(?=.{4,12})')
			return regExp.test(value) ? setNote({ ...note, [field]: '' }) : setNote({ ...note, [field]: '' })
		} else {
			return !value.trim() && field !== 'password' ? setNote({ ...note, [field]: `${field}` }) : setNote({ ...note, [field]: '' })
		}
	}
  const handleClick = e => {
		setAlertNote('')
		if (!(account && name && email && password && checkPassword)) return setAlertNote({ note: '', type: 'error' })
		if (password !== checkPassword) {
			setAlertNote({ note: '', type: 'error' })
			setPassword('')
			return setCheckPassword('')
		}
		if (name?.length > 50) return setAlertNote({ note: '', type: 'error' })
		const form = { account, name, email, password, checkPassword }

		Myaxios()
			.post(`/users`, form)
			.then(e => {
				console.log('', e.status)
        setAlertNote({ note: '', type: 'suc' })
        setTimeout(() => {
					navigate('/login')
				}, 2000)
				
			})
      .catch(err => {
        console.error(err)
				setAlertNote({ note: takeErrMsg(err), type: 'error' })
			})
	}
	useEffect(() => {
		updateNoteField('account', account)
	}, [account])
	useEffect(() => {
		updateNoteField('name', name)
		if (name?.length > 50) {
			setNote({ ...note, name: '50' })
		}
	}, [name])
	useEffect(() => {
    updateNoteField('email', email)
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
		if (!emailRegex.test(email)) {
			setNote({ ...note, email: '' })
		}
	}, [email])
	useEffect(() => {
		updateNoteField('password', password)
	}, [password])

	return (
		<div className={styles.container}>
			{alertNote && (
				<div
					onClick={() => {
						setAlertNote('')
						
					}}>
					<Alert alertNote={alertNote.note} alertType={alertNote.type} />
				</div>
			)}
			<div>
				<AcLogo />
			</div>
			<h3 className={styles.title}></h3>
			{/* AuthInput group */}
			<div className={styles['authinput-group']}>
				<AuthInput label='' type='text' value={account} placeholder='' onChange={setAccount} note={note.account} />

				<AuthInput label='' type='text' value={name} placeholder='' onChange={setName} note={note.name} />

				<AuthInput label='Email' type='text' value={email} placeholder='Email' onChange={setEmail} note={note.email} />

				<AuthInput label='' type='password' value={password} placeholder='' onChange={setPassword} note={note.password} />

				<AuthInput label='' type='password' value={checkPassword} placeholder='' onChange={setCheckPassword} />
			</div>
			<div className={styles['auth-button']} onClick={handleClick}>
				<Button styleName='lg-bg-logo'></Button>
			</div>
			{/* Auth Link */}
			<Link to='/login' className={styles['auth-link']}>
				<div className={styles['auth-link-text']}></div>
			</Link>
		</div>
	)
}

export default RegisterPage
