import React, { useState } from 'react';
import axios from 'axios';
import styles from './AdminLoginPage.module.scss';
import { Link, useNavigate } from 'react-router-dom';
import { API_URL } from '../../constants';

// Component
import { ReactComponent as AcLogo } from '../../assets/icons/AcLogo.svg';
import AuthInput from '../../Components/AuthInput/index';
import Button from '../../Components/Button';

function AdminLoginPage({ setUserData }) {
  const navigate = useNavigate();
  //
  const [account, setAccount] = useState('');
  const [password, setPassword] = useState('');
  const [note, setNote] = useState({});
  //
  const handleClick = () => {
    if (!password || !account) return setNote({ password: '' });

    axios
      .post(`${API_URL}/admin/login`, { account, password })
      .then((response) => {
        const { token, user } = response.data.data;

        if (token) {
          localStorage.setItem('token', token);
          setUserData({ token, user });
          navigate('/admin/tweets');
        }
        setNote({});
      })
      .catch((error) => {
        if (error.response.status === 400) {
          setNote({ account: '' });
        }
        if (error.response.status === 401) {
          setNote({ password: '' });
        }
      });
  };

  return (
    <div className={styles.container}>
      <div>
        <AcLogo />
      </div>
      <h3 className={styles.title}></h3>
      {/* AuthInput group */}
      <div className={styles['authinput-group']}>
        <AuthInput label='' type='text' value={account} placeholder='' onChange={setAccount} note={note.account} />

        <AuthInput label='' type='password' value={password} placeholder='' onChange={setPassword} note={note.password} />
      </div>
      {/*  */}
      <div className={styles['auth-button']} onClick={handleClick}>
        <Button styleName='lg-bg-logo'></Button>
      </div>
      {/*  */}
      <div className={styles['auth-link-box']}>
        <Link to='/login' className={styles['auth-link']}>
          <div className={styles['auth-link-text']}></div>
        </Link>
      </div>
    </div>
  );
}

export default AdminLoginPage;
